import React, { useCallback, useEffect, useMemo, useState } from 'react';
import classNames from 'classnames';
import { useDebounce } from 'use-debounce';
import {
  Button, Container, Divider, Form, Grid, Header, Input, Label, Loader, Modal,
} from 'semantic-ui-react';

import withAuth from './with-auth';
import useField from './use-field';

import styles from './App.module.css';

import 'semantic-ui-css/semantic.min.css';

const FORMATS_MAP = [
  ['anagrams', 'Анаграммы'],
  ['plusograms', 'Плюсограммы'],
  ['metagrams', 'Метаграммы'],
  ['metanagrams', 'Метанаграммы'],
  ['tlenograms', 'Тленограммы'],
  ['logogrifs', 'Логогрифы'],
  ['logogrifs2', 'Логогрифы +2'],
  ['metagrifs', 'Метагрифы'],
  ['brukvas', 'Брюквы'],
  ['tikvas', 'Тыквы'],
  ['hybrids3', 'Гибриды (3)'],
  ['hybrids4', 'Гибриды (4)'],
  ['sharadas', 'Шарады'],
  ['golovahvosts', 'Головахвосты'],
];

const DICTIONARIES_MAP = [
  ['sanstv', 'sanstv.ru'],
  ['sociation', 'sociation.org'],
];

const isAnagram = (word1, word2) => {
  if (word1.length !== word2.length) {
    return false;
  }

  const word1Sorted = word1.split('').sort().join('');
  const word2Sorted = word2.split('').sort().join('');

  return word1Sorted === word2Sorted;
}

const isPlusogram = (word1, word2) => {
  if (word2.length - word1.length !== 1) {
    return false;
  }

  const word1Sorted = word1.split('').sort().join('');
  const word2Sorted = word2.split('').sort().join('');

  let hasDifference = false;
  for (let i = 0; i < word2Sorted.length; i++) {
    if (word1Sorted.charAt(i - +hasDifference) !== word2Sorted.charAt(i)) {
      if (hasDifference) {
        return false;
      }

      hasDifference = true;
    }
  }

  return hasDifference;
}

const isMetagram = (word1, word2) => {
  if (word1.length !== word2.length) {
    return false;
  }

  let hasDifference = false;
  for (let i = 0; i < word1.length; i++) {
    if (word1.charAt(i) !== word2.charAt(i)) {
      if (hasDifference) {
        return false;
      }

      hasDifference = true;
    }
  }

  return hasDifference;
}

const isMetanagram = (word1, word2) => {
  if (word1.length !== word2.length) {
    return false;
  }

  for (let i = 0; i < word1.length; i++) {
    word2 = word2.replace(word1.charAt(i), '');
  }

  return word2.length === 1;
}

const isTlenogram = (word1, word2) => {
  if (word2.length - word1.length !== 1) {
    return false;
  }

  for (let i = 0; i < word1.length; i++) {
    word2 = word2.replace(word1.charAt(i), '');
  }

  return word2.length === 2;
}

const isLogogrif = (word1, word2) => {
  if (word2.length - word1.length !== 1) {
    return false;
  }

  let hasDifference = false;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - +hasDifference) !== word2.charAt(i)) {
      if (hasDifference) {
        return false;
      }

      hasDifference = true;
    }
  }

  return hasDifference;
}

const isLogogrif2 = (word1, word2) => {
  if (word2.length - word1.length !== 2) {
    return false;
  }

  let totalDifferences = 0;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - totalDifferences) === word2.charAt(i)) {
      if (totalDifferences === 1) {
        return false;
      }
    } else {
      if (totalDifferences === 2) {
        return false;
      }

      totalDifferences++;
    }
  }

  return totalDifferences === 2;
}

const isMetagrif = (word1, word2) => {
  if (word2.length - word1.length !== 1) {
    return false;
  }

  let totalDifferences = 0;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - +!!totalDifferences) !== word2.charAt(i)) {
      totalDifferences++;

      if (totalDifferences > 2) {
        break;
      }
    }
  }

  if (totalDifferences === 2) {
    return true;
  }

  totalDifferences = 0;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - +(totalDifferences === 2)) !== word2.charAt(i)) {
      if (totalDifferences === 2) {
        return false;
      }

      totalDifferences++;
    }
  }

  return totalDifferences === 2;
}

const isBrukva = (word1, word2) => {
  if (word2.length - word1.length !== 1) {
    return false;
  }

  let totalDifferences = 0;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - +!!totalDifferences) === word2.charAt(i)) {
      if (totalDifferences === 1) {
        return false;
      }
    } else {
      if (totalDifferences === 2) {
        return false;
      }

      totalDifferences++;
    }
  }

  return totalDifferences === 2;
}

const isTikva = (word1, word2) => {
  if (word2.length - word1.length !== 1) {
    return false;
  }

  let totalDifferences = 0;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - +!!totalDifferences) === word2.charAt(i)) {
      if (totalDifferences === 1 || totalDifferences === 2) {
        return false;
      }
    } else {
      if (totalDifferences === 3) {
        return false;
      }

      totalDifferences++;
    }
  }

  return totalDifferences === 3;
}

const createHybrid3 = (word1, word2) => {
  const intersection = word1.slice(-3);
  if (word2.slice(0, 3) === intersection) {
    return word1.slice(0, -3) + intersection.toUpperCase() + word2.slice(3);
  }

  return null;
}

const createHybrid4 = (word1, word2) => {
  const intersection = word1.slice(-4);
  if (word2.slice(0, 4) === intersection) {
    return word1.slice(0, -4) + intersection.toUpperCase() + word2.slice(4);
  }

  return null;
}

const createSharada = (word1, word2, dictionarySet) => {
  const word = word1 + word2;
  if (dictionarySet.has(word)) {
    return word;
  }

  return null;
}

const createGolovahvosts = (word1, word2, dictionarySet) => {
  if (word1.length < 3 || word2.length < 3) {
    return [];
  }

  const golovahvosts = [];
  for (let i = word1.length; i >= 3; i--) {
    const word1Tail = word1.slice(-i);

    for (let j = word2.length; j >= 3; j--) {
      const word = word1Tail + word2.slice(0, j);
      if (dictionarySet.has(word)) {
        golovahvosts.push(
          word1.slice(0, word1.length - i) + word.toUpperCase() + word2.slice(j - word2.length),
        );
      }
    }
  }

  return golovahvosts;
}

const createEmptyFormats = (selectedFormatsSet) => {
  return FORMATS_MAP.reduce((result, [format]) => {
    if (selectedFormatsSet.has(format)) {
      return {
        ...result,
        [format]: [],
      }
    }

    return result;
  }, {});
};

const getFormats = (word, words, dictionarySet, selectedFormatsSet, isIntersection = false) => {
  const formats = createEmptyFormats(selectedFormatsSet);

  if (word.length > 0) {
    words.forEach((wordDictionary) => {
      if (wordDictionary === word) {
        return;
      }

      if (formats.anagrams && isAnagram(word, wordDictionary)) {
        formats.anagrams.push(isIntersection ? `${word} ${wordDictionary}` : wordDictionary);
      }

      if (
        formats.plusograms &&
        (isPlusogram(word, wordDictionary) || isPlusogram(wordDictionary, word))
      ) {
        formats.plusograms.push(isIntersection ? `${word} ${wordDictionary}` : wordDictionary);
      }

      if (formats.metagrams && isMetagram(word, wordDictionary)) {
        formats.metagrams.push(isIntersection ? `${word} ${wordDictionary}` : wordDictionary);
      }

      if (
        formats.metanagrams &&
        (isMetanagram(word, wordDictionary) || isMetanagram(wordDictionary, word))
      ) {
        formats.metanagrams.push(isIntersection ? `${word} ${wordDictionary}` : wordDictionary);
      }

      if (
        formats.tlenograms &&
        (isTlenogram(word, wordDictionary) || isTlenogram(wordDictionary, word))
      ) {
        formats.tlenograms.push(isIntersection ? `${word} ${wordDictionary}` : wordDictionary);
      }

      if (
        formats.logogrifs &&
        (isLogogrif(word, wordDictionary) || isLogogrif(wordDictionary, word))
      ) {
        formats.logogrifs.push(isIntersection ? `${word} ${wordDictionary}` : wordDictionary);
      }

      if (
        formats.logogrifs2 &&
        (isLogogrif2(word, wordDictionary) || isLogogrif2(wordDictionary, word))
      ) {
        formats.logogrifs2.push(isIntersection ? `${word} ${wordDictionary}` : wordDictionary);
      }

      if (
        formats.metagrifs &&
        (isMetagrif(word, wordDictionary) || isMetagrif(wordDictionary, word))
      ) {
        formats.metagrifs.push(isIntersection ? `${word} ${wordDictionary}` : wordDictionary);
      }

      if (
        formats.brukvas &&
        (isBrukva(word, wordDictionary) || isBrukva(wordDictionary, word))
      ) {
        formats.brukvas.push(isIntersection ? `${word} ${wordDictionary}` : wordDictionary);
      }

      if (
        formats.tikvas &&
        (isTikva(word, wordDictionary) || isTikva(wordDictionary, word))
      ) {
        formats.tikvas.push(isIntersection ? `${word} ${wordDictionary}` : wordDictionary);
      }

      if (formats.hybrids3 && isIntersection) {
        let hybrid3 = createHybrid3(word, wordDictionary);
        if (hybrid3) {
          formats.hybrids3.push(hybrid3);
        }

        hybrid3 = createHybrid3(wordDictionary, word);
        if (hybrid3) {
          formats.hybrids3.push(hybrid3);
        }
      }

      if (formats.hybrids4 && isIntersection) {
        let hybrid4 = createHybrid4(word, wordDictionary);
        if (hybrid4) {
          formats.hybrids4.push(hybrid4);
        }

        hybrid4 = createHybrid4(wordDictionary, word);
        if (hybrid4) {
          formats.hybrids4.push(hybrid4);
        }
      }

      if (formats.sharadas) {
        let sharada = createSharada(word, wordDictionary, dictionarySet);
        if (sharada) {
          formats.sharadas.push(sharada);
        }

        sharada = createSharada(wordDictionary, word, dictionarySet);
        if (sharada) {
          formats.sharadas.push(sharada);
        }
      }

      if (formats.golovahvosts && isIntersection) {
        formats.golovahvosts.push(
          ...createGolovahvosts(word, wordDictionary, dictionarySet),
          ...createGolovahvosts(wordDictionary, word, dictionarySet),
        );
      }
    });
  }

  return formats;
}

const getIntersectionFormats = (words1, words2, dictionarySet, selectedFormatsSet) => {
  return words1.reduce((result, word1) => {
    const formats = getFormats(word1, words2, dictionarySet, selectedFormatsSet, true);

    Object.keys(result).forEach((format) => {
      result[format].push(...formats[format]);
    });

    return result;
  }, createEmptyFormats(selectedFormatsSet));
}

const App = () => {
  const [database, setDatabase] = useState(null);

  const [selectedFormatsSet, setSelectedFormatsSet] = useState(
    new Set(FORMATS_MAP.map(([format]) => format)),
  );

  const [selectedDictionary, handleSelectedDictionaryChange] = useField('sanstv');

  const [word1, handleWord1Change] = useField('');
  const [word2, handleWord2Change] = useField('');

  const [word1Debounced] = useDebounce(word1, 500);
  const [word2Debounced] = useDebounce(word2, 500);

  const word1Clean = useMemo(() => word1Debounced.trim().toLowerCase().replace('ё', 'е'), [
    word1Debounced,
  ]);

  const word2Clean = useMemo(() => word2Debounced.trim().toLowerCase().replace('ё', 'е'), [
    word2Debounced,
  ]);

  const formats = useMemo(() => {
    if (!database) {
      return {};
    }

    const dictionarySet = database.dictionary[selectedDictionary].set;

    if (word2Clean) {
      const words1 = word1Clean.endsWith('!')
        ? [word1Clean.slice(0, -1)]
        : [word1Clean, ...(database.associations[word1Clean] || [])];

      const words2 = word2Clean.endsWith('!')
        ? [word2Clean.slice(0, -1)]
        : [word2Clean, ...(database.associations[word2Clean] || [])];

      return getIntersectionFormats(words1, words2, dictionarySet, selectedFormatsSet);
    }

    return getFormats(
      word1Clean,
      database.dictionary[selectedDictionary].array,
      dictionarySet,
      selectedFormatsSet,
    );
  }, [database, selectedFormatsSet, selectedDictionary, word1Clean, word2Clean]);

  const handleSelectedFormatChange = useCallback((_, { value }) => {
    if (selectedFormatsSet.has(value)) {
      setSelectedFormatsSet(new Set([...selectedFormatsSet].filter(format => format !== value)));
    } else {
      setSelectedFormatsSet(new Set([...selectedFormatsSet, value]));
    }
  }, [selectedFormatsSet]);

  useEffect(() => {
    (async () => {
      const { dictionaries, associations } = await import('./database.json');

      setDatabase({
        dictionary: {
          sanstv: {
            array: dictionaries.sanstv,
            set: new Set(dictionaries.sanstv),
          },
          sociation: {
            array: dictionaries.sociation,
            set: new Set(dictionaries.sociation),
          },
        },
        associations,
      });
    })();
  }, []);

  if (!database) {
    return <Loader active>Загружаю базу...</Loader>;
  }

  return (
    <>
      <div className={styles.header}>
        <Container>
          <Grid padded>
            <Grid.Row>
              <Grid.Column width={8}>
                <Form>
                  <Input
                    fluid
                    autoFocus
                    value={word1}
                    placeholder="Слово"
                    size="big"
                    onChange={handleWord1Change}
                  />
                </Form>
              </Grid.Column>
              <Grid.Column width={7}>
                <Form>
                  <Input
                    fluid
                    value={word2}
                    placeholder="Второе слово (пересечение по ассоциатору)"
                    size="big"
                    onChange={handleWord2Change}
                  />
                </Form>
              </Grid.Column>
              <Grid.Column width={1}>
                <Modal size="mini" trigger={<Button size="big" icon="setting" />}>
                  <Header icon="setting" content="Настройки" />
                  <Modal.Content>
                    <Form>
                      <Form.Group grouped>
                        <label>Форматы</label>
                        {FORMATS_MAP.map(([format, formatLabel]) => (
                          <Form.Checkbox
                            key={format}
                            value={format}
                            label={formatLabel}
                            checked={selectedFormatsSet.has(format)}
                            onChange={handleSelectedFormatChange}
                          />
                        ))}
                      </Form.Group>
                      <Form.Group grouped>
                        <label>Словарь</label>
                        {DICTIONARIES_MAP.map(([dictionary, dictionaryLabel]) => (
                          <Form.Radio
                            key={dictionary}
                            value={dictionary}
                            label={dictionaryLabel}
                            checked={dictionary === selectedDictionary}
                            onChange={handleSelectedDictionaryChange}
                          />
                        ))}
                      </Form.Group>
                    </Form>
                  </Modal.Content>
                </Modal>
              </Grid.Column>
            </Grid.Row>
          </Grid>
        </Container>
      </div>
      <div className={styles.content}>
        <Container>
          <Grid padded>
            {FORMATS_MAP.filter(([format]) => formats[format] && formats[format].length > 0).map(
              ([format, formatLabel]) => (
                <Grid.Row key={format}>
                  <Grid.Column>
                    <Divider horizontal>
                      <Header as="h4" className={styles.title}>
                        {formatLabel}
                      </Header>
                    </Divider>
                    <div className={styles.words}>
                      {formats[format].map((word, i) => (
                        <Label
                          horizontal
                          key={word}
                          className={classNames(styles.word, i % 2 && styles.odd)}
                        >
                          {word}
                        </Label>
                      ))}
                    </div>
                  </Grid.Column>
                </Grid.Row>
              ),
            )}
          </Grid>
        </Container>
      </div>
    </>
  );
};

export default withAuth(App);
